.header-color 
{
background: #141E30;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #243B55, #141E30);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #243B55, #141E30); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

.projects-grid {
  padding-top: 70px;
  display: flex;
}

.landing-grid 
{
background: #4b6cb7;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #182848, #4b6cb7);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #182848, #4b6cb7); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

text-align: center;
position: fixed;
top: 0;
left: 0;
width: 100%;
height: 100%;
}

.projectspage
{
  
position: fixed;
left: 0;
width: 100%;
height: 100%;
}


.tabspage
{
background: #667db6;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #667db6, #0082c8, #0082c8, #667db6);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #667db6, #0082c8, #0082c8, #667db6); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

padding-right:40px;
}

.tabspage h1
{
  font-size: 14px;
  color: white;
    margin-right: 50px;
  margin-left: 50px;
  font-weight: bold;
}

.avatar-img 
{

  height: 250px;
  padding-top: 7em;
}

.navcenter i
{
  font-size: 40px;
  float:left;
  padding-right: 470px;
}

.navcenter a
{
  margin: auto;
  padding-right: 40px;
  font-size: 20px;
  color: white;
  padding: 40px;
}

.navcenter a:hover
{
  color: grey;
}

.banner-text
{
  background-color: black;
  opacity: .8;
  width: 45%;
  margin: auto;
  border-radius: 10px;
  padding-bottom: 20px;
}

.banner-text h1
{
  font-size: 66px;
  font-weight: bold;
  font-weight: white;
  color: white;
}

.banner-text hr 
{
  border-top: 3px solid white;
  width: 60%;
  margin:auto;
}

.banner-text p
{
  color: white;
  font-size: 22px;
  padding: 1em;
}

.social-links
{
  display:flex;
  justify-content: space-between;
  width: 24%;
  margin:auto;
}

.social-links i 
{
  color:white;
  font-size: 6em;
}

.social-links i:hover
{
  color: grey;
}

/*CONTACT*/

.contact-body 
{
  margin:auto;
  background:grey;
  position: fixed;
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
  background: #4b6cb7;
  background: -webkit-linear-gradient(to right, #182848, #4b6cb7);
  background: linear-gradient(to right, #182848, #4b6cb7);
}

.contact-grid 
{
  text-align: center;
  justify-content: center;
  width: 85%;
  height: 85%%;
  background: white;
  color: black;
  margin-bottom: 20px;
  border-radius: 20px;
}

.contact-grid h2
{
  font-family: 'Anton', sans-serif;
}

.contact-grid p
{
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}

.contact-grid hr
{
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}

.contact-list i 
{
  font-size: 66px;
  padding-right: 5rem;
  padding-bottom: 20px;
}
